$colorWhite: #fff;
$colorBlack: #000;
$colorBlackLight: #191919;
$colorYellow: #E6EB54;
$colorOrange: #F5853A;
$colorOrangeLight: #F7B455;
$colorBlue: #67A0F4;
$colorBlueLight: #ACC6DD;
$colorDarkGrey: #282828;
$colorGrey: #849B9A;
$colorGreyLight: #B7B4AE;
$colorBrown: #66393D;
$colorPink: #FF7276;
$colorPinkLight: #66393D;
$colorGreen: #4D9894;
$colorGreenLight: #9DE7D7;
$colorGreenLight2: #EEFAEC;
$colorRed: #F04939;
$colorRedLight: #EA7474;

$colorBgAnnouncement: #F3F9F9;

html,
body {
	height: 100%;
}

body {
	color: $colorDarkGrey;
	font-size: 14px;
	font-family: 'TheNextPanBook', sans-serif;
	line-height: 1.25;
}

* {
	outline: none;
	box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
	font-size: 1em;
	line-height: 1;
	margin: 0;
}

img {
	vertical-align: top;
	max-width: 100%;
}

a {
	text-decoration: none;
	transition: none;

	&:hover {
		text-decoration: none;
	}
}

ul,
ol {
	list-style-type: none;
	list-style-position: inside;
	padding: 0;
	margin: 0;
}

p {
	margin: 0;
}

button {
	outline: none;

	&:focus {
		outline: none;
	}
}

[class^='icon-'],
[class*=' icon-'] {
	position: relative;
	vertical-align: middle;
}

.wrapper {
	position: relative;
	min-width: 320px;
	min-height: 100%;
	//overflow: hidden;
}

.hidden {
	display: none !important;
}

body {
	.bg--black {
		background-color: $colorBlack;
		color: $colorWhite;
	}

	.bg--pink {
		background-color: rgba(245, 223, 236, 1);
	}

	.bg--green-light2 {
		background-color: $colorGreenLight2;
	}

	.bg--grey-light {
		background-color: #F6F6F6;
	}

	.bg--grey-light2 {
		background-color: #EDF2EC;
	}

	.bg--yellow-light {
		background-color: #FFEEAD;
	}

	.bg--yellow {
		background-color: #FBF4DA;
	}

	.bg--blue-light {
		background-color: #F2F4F3;
	}

	.bg--blue-light2 {
		background-color: $colorBgAnnouncement;
	}

	.bg--blue {
		background-color: #E8F3F3;
	}

	.text--white {
		color: $colorWhite;
	}
}

.overflow__hidden {
	overflow: hidden;
}

.overflow__layout {
	position: relative;

	&:before {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.7);
		user-select: none;
		pointer-events: none;
		z-index: 101;
	}
}

.container {
	max-width: 1847px;
	padding: 0 15px;
	margin: 0 auto;
	@include rmax(1500) {
		max-width: 1350px;
	}
	@include rmax(768) {
		padding: 0 20px;
	}

	&.container--small {
		max-width: 1018px;
	}
}

.container-large {
	max-width: 1440px;
	padding: 0;
	margin: 0 auto;
}

.container-medium {
	max-width: 1350px;
	margin: 0 auto;
}

.container-fluid {
	width: 100%;
	padding: 0 15px;
}

.container-full {
	width: 100%;
	max-width: 1920px;
	margin: 0 auto;
}

.desktop-hide {
	@include rmin(769) {
		display: none !important;
	}
}

.tablet-hide {
	@include rmax(1280) {
		display: none !important;
	}
}

.tablet-show {
	@include rmin(1281) {
		display: none !important;
	}
}

.mobile-hide {
	@include rmax(768) {
		display: none !important;
	}
}

.announcement {
	position: relative;
	overflow: hidden;
	z-index: 24;

	&__wrapper {
		background-color: $colorBgAnnouncement;
	}

	&__link {
		display: block;
	}

	&__text {
		font-family: "TheNextPanBookLight";
		color: $colorBlack;
		font-size: 18px;
		line-height: 110%;
		font-weight: 300;
		letter-spacing: 0em;
		text-align: center;
		text-transform: none;
		padding: 2px 20px;

		height: 50px;
		display: flex;
		align-items: center;
		justify-content: center;

		span {
			display: flex;
			align-items: center;
			justify-content: center;

			img {
				margin: 0 10px;
			}

			b {
				margin-right: 4px;
				font-weight: 600;
			}
		}

		@include rmax(768) {
			font-size: 16px;
			padding: 7px 20px 6px;
		}
	}
}

.breadcrumbs {
	ul {
		white-space: nowrap;
		overflow-x: auto;

		li {
			position: relative;
			display: inline;
			vertical-align: middle;
			font-size: 15px;
			font-style: normal;
			font-weight: 400;
			line-height: 144%;
			letter-spacing: 0.02em;
			color: #959595;

			&:not(:last-child):after {
				content: '>';
				position: relative;
				vertical-align: middle;
				top: -1px;
				color: #959595;
				font-size: 18px;
				line-height: 0;
				margin: 0 10px;
			}

			a {
				color: #959595;
				transition: color 0.3s ease;

				&:hover {
					color: #9DE7D7;
				}
			}
		}
	}
}

h2 {
	font-size: 63px;
	font-weight: 400;
	line-height: 120%;
	letter-spacing: -0.01em;
	text-align: center;
}

section {
	&:not(.not-overflow) {
		overflow: hidden;
	}
}

.section-header {
