		margin-bottom: 35px;
	}
	margin-bottom: 15px;
	text-align: center;
	&--left{
		text-align: left;
	}
	&--right{
		text-align: right;
	}
	&__subtitle {
		font-family: 'TheNextPanBookLight';
		font-size: 20px;
		font-weight: 300;
		line-height: 110%;
		letter-spacing: 0em;
		margin-bottom: 5px;
		color: $colorDarkGrey;
		@include rmax(1400) {
			font-size: 18px;
			letter-spacing: 0.05em;
		}
		@include rmax(768) {
			font-size: 14px;
			letter-spacing: 0.05em;
		}
	}
	&__title {
		font-size: 45px;
		font-weight: 400;
		line-height: 120%;
		letter-spacing: -0.01em;
		margin: 0 0 15px;
		@include rmax(1400) {
			font-size: 40px;
		}
		@include rmax(1280) {
			font-size: 35px;
		}
		@include rmax(768) {
