		&-big {
			font-size: 60px;
			font-style: normal;
			font-weight: 400;
			line-height: 125%;
			letter-spacing: -0.01em;
			margin: 0 0 7px;
			@include rmax(1400) {
				font-size: 50px;
			}
			@include rmax(1280) {
				font-size: 45px;
			}
			@include rmax(768) {
				margin: 0 0 5px;
				font-size: 35px;
				line-height: 100%;
			}
			@include rmax(575) {
