		font-size: 20px;
		font-style: normal;
		font-weight: 300;
		line-height: 175%;
		letter-spacing: 0.02em;
		margin: 0 auto;
		color: #797979;
		@include rmax(1400) {
			font-size: 18px;
		}
		@include rmax(1280) {
			font-size: 17px;
		}
		@include rmax(768) {
			font-size: 16px;
		}

		p {
			&:not(:last-child) {
				margin-bottom: 30px;
				@include rmax(1400) {
					margin-bottom: 25px;
				}
				@include rmax(1280) {
