		&--big {
			font-family: 'TheNextPanBookLight';
			font-size: 25px;
			font-style: normal;
			font-weight: 300;
			line-height: 130%;
			letter-spacing: 0em;
			margin: 0 auto;
			@include rmax(1400) {
				font-size: 24px;
			}
			@include rmax(1280) {
				font-size: 22px;
			}
			@include rmax(768) {
				font-size: 20px;
			}
			@include rmax(575) {
				font-size: 18px;
			}
		}
	}

}

.section-button--center {
	display: flex;
	justify-content: center;
}

