.page-width {
	max-width: 1171px;
	margin: 0 auto;
}

/*
SLIDER
 */

.slick-dots {
	position: relative;
	padding: 40px 0;
	display: flex;
	justify-content: center;
	z-index: 1;
	bottom: auto;

	li {
		width: auto;
		height: auto;
		line-height: 0;
		margin: 0 11px;
		@include rmax(768) {
			margin: 0 7px;
		}

		&:not(.slick-active) {
			button {
				cursor: pointer;
			}
		}

		button {
			position: relative;
			width: 11px;
			height: 11px;
			color: transparent;
			border: 2px solid #797979;
			background-color: #797979;
			border-radius: 50%;
			padding: 0;
			@include rmax(768) {
				width: 8px;
				height: 8px;
			}

			&:before {
				display: none;
			}

			&:before {
				content: none;
			}
		}

		&.slick-active {
			button {
				border: 2px solid $colorDarkGrey;
				background: $colorDarkGrey;
			}
		}
	}
}

.slick-arrow {
	position: absolute;
	background: transparent;
	top: 50%;
	width: 45px;
	height: 45px;
	color: #7A7A7A;
	font-size: 0;
	transform: translateY(-50%);
	transition: transform 0.3s ease;
	border: none;
	z-index: 5;
	transition: all 0.3s ease-in-out;

	&:not(:disabled) {
		cursor: pointer;

	}


	@include rmin(1280) {
		&:hover {
			transform: scale(1.325) translateY(-50%);
		}
	}

	&:before {
		color: #7A7A7A;
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		font-size: 44px;
		font-family: 'svgfont', sans-serif;
		transform: translate(-50%, -50%);
		opacity: 1;
	}

	&.slick-disabled {
		cursor: auto;

		&:before {
			color: #d8d1d1;
		}

		&:hover {
			transform: translateY(-50%);
		}
	}
}

.slick-prev {
	left: 0;
	padding: 60px 60px 60px 0;
	@include rmax(1280) {
		padding: 20px 20px 20px 0;
	}

	&:before {
		content: '\EA06';
	}

}

.slick-next {
	right: 0;
	padding: 60px 0px 60px 60px;
	@include rmax(1280) {
		padding: 20px 0px 20px 20px;
	}

	&:before {
		content: '\EA02';
	}
}

.no-hover {
	.slick-arrow {
		&:hover {
			transform: translateY(-50%);
		}
	}

	.slick-prev {
		padding: 0;
	}

	.slick-next {
		padding: 0;
	}
}

