.el-plus {
	position: absolute;
	bottom: 15px;
	right: 13px;
	width: 36px;
	height: 36px;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	border-radius: 50%;
	line-height: 100%;
	background-color: #C4C4C4;
	font-size: 35px;
	font-weight: 400;
	color: $colorWhite;
	transition: all 0.3s ease-in-out;

	&:hover {
		transform: scale(1.55);
	}

	@include rmax(768) {
		width: 33px;
		height: 33px;
		bottom: 14px;
		right: 12px;
		font-size: 33px;
	}
}

.wave-text {
	padding-top: 10px;
	color: #265A62;
	margin: 0;
	font-size: 26px;
	font-weight: 400;
	line-height: 110%;
	position: absolute;
	top: -20%;
	width: 100%;
	letter-spacing: 0.01em;
	@media (max-width: 1024px) {
		display: none;
		font-size: 16px;
		top: 0%;
	}

	span {
		display: inline-block;
		animation: jump 3s ease-in-out 0s infinite alternate;
		@for $i from 1 through 100 {
			&:nth-child(#{$i}) {
				animation-delay: ($i * 0.15) * 1s
			}
		}
	}
}

@keyframes jump {
	0% {
		transform: translateY(0);
	}
	100% {
		transform: translateY(2em);
	}
}

