@keyframes introZoomInSecond {
	0% {
		transform: scale(0);
		opacity: 1;
	}
	30% {
		transform: scale(0);
	}
	40% {
		transform: scale(1);
		opacity: 1;
	}
	90% {
		opacity: 1;
	}
	100% {
		transform: scale(1);
		opacity: 0;
	}
}

@keyframes introZoomInThird {
	0% {
		transform: scale(0);
		opacity: 1;
	}
	40% {
		transform: scale(0);
	}
	50% {
		transform: scale(1);
		opacity: 1;
	}
	90% {
		opacity: 1;
	}
	100% {
		transform: scale(1);
		opacity: 0;
	}
}

@keyframes introSlideLeft {
	0% {
		right: -100vw;
		opacity: 1;
	}
	6% {
		right: -100vw;
	}
	16% {
		right: 0;
	}
	90% {
		opacity: 1;
	}
	100% {
		right: 0;
		opacity: 0;
	}
}

@keyframes introSlideLeftMobile {
	0% {
		left: 200%;
		opacity: 1;
	}
	6% {
		right: 200%;
	}
	16% {
		left: 50%;
	}
	90% {
		opacity: 1;
	}
	100% {
		left: 50%;
		opacity: 0;
	}
