.intro-section {
	position: relative;

	&__background {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		//height: 61.3vw;
		//max-height: 1100px;
		height: 100%;

		&-gradient {
			position: absolute;
			top: 0;
			left: 50%;
			//width: 100vw;
			//height: 53vw;
			//max-height: 940px;

			width: 100vw;
			height: 60vw;
			max-height: 1127px;
			transform: translateX(-50%);
			@include rmax(768) {
				height: 140vw;
				max-height: 680px;
			}
			@include rmax(575) {
				height: 140vw;
				max-height: 680px;
			}

			img {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				object-fit: cover;
				object-position: center bottom;
			}
		}

		&-wave {
			position: absolute;
			right: -100vw;
			bottom: 0;
			width: 65.56vw;
			height: 31.32vw;
			transition: right 0.5s ease-in-out, opacity 0.3s ease-in-out;
			animation: introSlideLeft 5s ease-in-out infinite;
			@include rmax(768) {
				left: 200%;
				right: auto;
				width: 170vw;
				height: 60vw;
				transform: translateX(-55%);
				transition: left 0.5s ease-in-out, opacity 0.3s ease-in-out;
				animation: introSlideLeftMobile 5s ease-in-out infinite;
			}
			@include rmax(575) {
				bottom: 15vw;
			}

			img {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				object-fit: cover;
				object-position: center top;
			}
		}
	}

	&__product {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;

		&-first,
		&-second,
		&-third {
			position: absolute;

			img {
				width: 100%;
				transform: scale(0);
				transition: transform 0.5s ease-in-out, opacity 0.3s ease-in-out;
			}
		}

		&-first {
			right: 33%;
			bottom: 31%;
			width: 13.8889vw;
			transform: rotate(-15deg);
			@include rmax(768) {
				width: 26.3334vw;
				right: auto;
				left: 50%;
				margin-left: -29vw;
				transform: rotate(-15deg) translateX(-50%);
			}
			@include rmax(575) {
				width: 29.3334vw;
			}

			img {
				animation: introZoomInFirst 5s ease-in-out infinite;
			}
		}

		&-second {
			right: 5%;
			bottom: 35%;
			width: 14.2361vw;
			transform: rotate(10.3deg);
			@include rmax(768) {
				width: 27.1334vw;
				right: auto;
				left: 50%;
				bottom: 31%;
				margin-left: 30vw;
				transform: rotate(10.3deg) translateX(-50%);
			}
			@include rmax(575) {
				width: 30.1334vw;
			}

			img {
				animation: introZoomInSecond 5s ease-in-out infinite;
			}
		}

		&-third {
			right: 18%;
			bottom: 31%;
