.trending-slider-fix {
	position: relative;
	top: 15px;

	&__overlay {
		display: none;
		position: fixed;
		height: 100%;
		width: 100vw;
		inset: 0px;
		overflow-x: hidden;
		overflow-y: auto;
		z-index: 2147483647;
		align-items: center;
		justify-content: center;
		flex-direction: column;

		background-color: rgba(0, 0, 0, 0.56);
		opacity: 0;
		transition: opacity 100ms ease-in-out 0s;
		animation: 300ms ease 0s 1 normal forwards running animOpacity;

		&.open {
			display: flex;
			justify-content: flex-start;
			align-items: center;
			@include rmin(1390) {
				justify-content: center;
			}
		}
	}

	&__wrap {
		position: relative;
		width: 100%;
		max-width: 70%;
		opacity: 0;
		margin: 0 auto;
		@include rmin(1280) {
			max-width: 800px;
		}
		@include rmax(575) {
			max-width: 100%;
		}

		&.open {
			opacity: 1;
		}

		@media only screen and (max-width: 768px) {
			.modal-header .close {
				top: 0;
				right: 10px;
			}
		}

		.slick-dots {
			padding: 0;
			top: -28px;
		}
	}

	&__item {
		transition: transform 0.3s linear;
		transition-delay: 0.5s;
		padding: 0 0 5px;
		@include rmax(1439) {
			padding: 0 0 10px;
		}
		@include rmax(575) {
			max-width: 340px;
			padding-bottom: 60px;
		}

		margin: 0 auto;
		background: rgba(255, 255, 255, 0.95);

	}

	.slick-slide {
		//transition: transform 300ms ease;
		&:not(.slick-current) {
			.trending-slider-fix__item {
				transform: scale(0.8);
				opacity: 0.8;
				transition-delay: 0s;
			}
		}

		//&.slick-cloned{
		//	transition: transform 300ms ease;
		//	transform: scale(0.8);
		//	opacity: 0.8;
		//}
		//&.slick-current {
		//	opacity: 1;
		//	transform: scale(1);
		//}
	}

	.slick-slide[aria-hidden="true"]:not(.slick-cloned) ~ .slick-cloned[aria-hidden="true"] {
		transform: scale(0.8);
		opacity: 0.8;
	}

	/* slide when active (when play last to first) */
	.slick-slide[aria-hidden="true"]:not([tabindex="-1"]) + .slick-cloned[aria-hidden="true"] {
		opacity: 1;
		transform: scale(1);
	}

	/* slide when active (when play first to last) */
	.slick-slide[aria-hidden="true"] + .slick-cloned[aria-hidden="true"] {
		opacity: 1;
		transform: scale(1);
	}

	.products-feature__image {
