/*<- - - - - -- - - - - - -- - PDP Modal ----- -- - - - - - - ->*/
.pdp-modal {
	&__dialog {
		&.modal-dialog {
			@include rmin(768) {
				max-width: 764px;
			}
		}
	}

	&__content {
		padding: 15px 50px 35px 50px;
		@include rmax(1400) {
			padding: 15px 40px 35px 40px;
		}
		@include rmax(768) {
			padding: 10px 20px 25px 20px;
		}
		@include rmax(575) {
			padding: 0 15px 15px 15px;
		}
	}

	&__slider {
		&-big {
			&-wrap {
				padding-bottom: 32px;
				@include rmax(768) {
					padding-bottom: 20px;
				}
				@include rmax(575) {
					padding-bottom: 15px;
				}

				.slick-arrow:before {
					font-size: 25px;
				}
			}

		}

		&-small {
			&-wrap {
				max-width: 362px;
				width: 100%;
				margin: 0 auto;
			}

			.slick-slide {
				&:not(.slick-current) {
					.pdp-modal-small__image {
						&:after {
							content: '';
							display: block;
							position: absolute;
							top: 0;
							bottom: 0;
							left: 0;
							right: 0;
							background: rgba(255, 255, 255, 0.5);
							z-index: 0;
						}
					}
				}
			}
		}
	}

	&-small {
		&__image {
			position: relative;
			margin: 0 4px;
			border: 1px solid #E0E0E0;
		}
	}

	&-big {
		&__image {
			//margin-right: 10px;
			padding: 0 5px;

			img {
				width: 100%;
				object-fit: cover;
			}
		}
	}
}
/*<- - - - - -- - - - - - -- - User-Profile ----- -- - - - - - - ->*/
.user-profile{
	&__inner{
		display: grid;
		align-items: center;
		grid-template-columns: 2fr 8fr;
		row-gap: 25px;
		column-gap: 40px;
		padding: 80px 0 0;
		@include rmax(1400) {
			padding: 60px 0 0;
			column-gap: 35px;
			grid-template-columns: 3fr 7fr;
		}
		@include rmax(768) {
			padding: 50px 0 0;
