.btn {
	position: relative;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	height: 51px;
	color: $colorBlack;
	background-color: $colorGreenLight;
	font-size: 20px;
	font-weight: 400;
	line-height: 230%;
	letter-spacing: 0em;
	text-align: center;
	text-decoration: none;
	padding: 0 20px;
	transition: all 0.3s ease-in-out;
	outline: none;
	border-width: 0;
	cursor: pointer;
	min-width: 200px;
	border-radius: 2px;
	@include rmax(768) {
		min-width: 156px;
		font-size: 16px;
		height: 40px;
	}

	&:hover {
		background-color: #C3FFF6;
		outline: none;
	}

	&:active,
	&:focus {
		outline: none;
		box-shadow: none;
		&:not(.btn--secondary){
			border: transparent;
		}

		&.footer-subscribe__form-group__submit{
			background-color: white;
			color: black ;
		}
		&.add-to-cart{
			background-color: #C3FFF6;
			color: #282828;
		}

		
	}

	&.disabled,
	&[disabled] {
		pointer-events: none;
		opacity: 0.5;
		cursor: auto;
	}
	&.subscribe-form-group__submit{
		background-color: #191919;
	}
	&--black {
			background-color: $colorBlack;
			color: $colorWhite;

			&:hover {
				background-color: #9DE7D7;
				color: #282828;
			}

			&:active, &:focus {
				background-color: #C3FFF6;
				color: #282828;
			}
	}
	&--black-light {
		background-color: #282828;
		color: $colorWhite;

		&:hover {
			background-color: #9DE7D7;
			color: #282828;
		}

		&:active, &:focus {
			background-color: #C3FFF6;
			color: #282828;
		}
	}

	&--white {
		background-color: $colorWhite;
		color: $colorBlack;

		&:hover {
			background-color: #000000;
			color: #ffffff;
		}

		&:active, &:focus {
			background-color: #797979;
			color: #ffffff;
		}
		&.btn--banner{
			&:active, &:focus {
				background-color: #797979;
				color: #ffffff;
			}
		}
	}

	&--big {
		font-size: 20px;
		letter-spacing: 0em;
		min-width: 200px;
		height: 54px;
		@include rmax(768) {
			min-width: 156px;
			font-size: 16px;
			height: 40px;
		}
	}

	&--medium {
		height: 59px;
		min-width: 280px;
		line-height: 255%;
	}

	&--secondary {
		height: 31px;
		font-size: 15px;
		font-weight: 600;
		letter-spacing: 0.02em;
		background-color: transparent;
		text-transform: uppercase;
		border-bottom: 2px solid #33BD9F;
		padding-right: 0;
		padding-left: 0;
		color: $colorDarkGrey;
		min-width: auto;
		i {
			align-items: center;
			margin-left: 10px;
			font-size: 14px;
			@include rmax(768) {
				font-size: 13px;
			}
		}

		@include rmax(768) {
			font-size: 14px;
			height: 30px;
		}
		&-big{
			height: 37px;
			font-size: 22px;
			@include rmax(1400) {
				height: 34px;
				font-size: 16px;
			}
			@include rmax(768) {
				height: 30px;
				font-size: 15px;
			}
		}

		&:hover {
			background-color: inherit;
			padding-bottom: 10px;
		}


	}
	&--out-stock{
		background-color: #797979;
		color: #ffffff;
		&:hover, &:focus, &:active{
			background-color: #797979 !important;
			color: #ffffff !important;
		}
	}
	&--bar{
		&:active, &:focus {
			background-color: #000000;
			color: #ffffff;
		}
	}
	&--add-product{
		&:active, &:focus {
			background-color: #000000;
			color: #ffffff;
			.price{
				color: #ffffff;
			}
		}
	}
}

.btn-wrap {
	padding-top: 30px;

	&--center {
		text-align: center;
	}
}

.header--fixed {
	.btn {
		&:active,
		&:focus {
			background-color: white;
			color: #282828;
		}
	}
}
