.footer{
	background-color: $colorBlackLight;
	color: $colorWhite;
	padding: 107px 0 45px;
	@include rmax(768){
		padding: 43px 0 34px;
	}
	&-inner{
		max-width: 1168px;
		width: 100%;
		margin: 0 auto;
	}
	&-grid{
		display: grid;
		grid-template-columns: 1.3fr 0.9fr 0.9fr 1fr;
		column-gap: 51px;
		@include rmax(768){
			grid-template-columns: 1fr;
			column-gap: 0px;
			row-gap: 40px;
		}
	}
	&-top{
		padding: 0 26px 35px 26px;
		@include rmax(768){
			padding: 0 0 30px 0px;
			.footer-grid{
				grid-template-columns: repeat(12, 1fr);
				.footer-column-1{
					grid-column: span 12;
				}
				.footer-column-2{
					grid-column: span 6;
				}
				.footer-column-3{
					grid-column: span 6;
				}
				.footer-column-4{
					display: flex;
					justify-content: space-between;
					order: -1;
					grid-column: span 12;
				}
			}
		}
	}
	&-center{
		padding: 38px 0 66px 26px;
		border-top: 2px solid $colorWhite;
		border-bottom: 2px solid $colorWhite;
		@include rmin(769){
			.footer-menu__title{
				&.no-menu{
					margin-bottom: 29px;
				}
			}
		}
		@include rmax(768){
			padding: 25px 0 35px 0px;
			border-bottom: none;
			.footer-menu__title{
				margin-bottom: 5px;
			}
			.footer-grid{
					row-gap: 0px;
			}
		}
	}
	&-bottom{
		padding: 46px 26px 0;
		display: flex;
		@include rmax(768){
			padding: 0px 0px 0;
			grid-template-columns: 1fr;
			row-gap: 10px;
			.footer-column{
				&:last-child{
					flex-direction: column-reverse;
				}
			}
			.footer-menu{
				display: flex;
				padding-top: 10px;

				li{
					a{
						text-decoration: underline;
					}
					&:not(:last-child){
						margin-right: 33px;
					}
				}
			}
		}
		.footer-column{
			display: flex;
			flex-direction: column;
			justify-content: flex-end;
			&:last-child{
				justify-content: center;
			}
		}


	}
	&__logo{
		display: flex;
		margin-right: 57px;
		max-width: 95px;
		max-height: 95px;
		width: 100%;
		img{
			width: 100%;
		}
	}
	&__copyright{
		font-family: 'TheNextPanBookLight';
		padding-top: 5px;
		font-size: 16px;
		font-weight: 400;
		line-height: 100%;
		letter-spacing: 0em;
		color: #ADADAD;
	}
	&-menu{
		&__title{
			font-size: 16px;
			line-height: 160%;
			letter-spacing: -0.02em;
			color: $colorWhite;
			margin-bottom: 22px;
			@include rmax(768){
				margin-bottom: 15px;
				&.acc-head{
					padding: 19px 5px 19px 0;
					display: flex;
					align-items: center;
					justify-content: space-between;
					margin-bottom: 0px;
					border-bottom: 1px solid white;
					&.active{
						border-bottom: none;
						i{
							transform: rotate(180deg);
						}
					}
					i{
						font-size: 12px;
						margin-left: 10px;
						transition: all 0.4s ease-in-out;
					}
				}
			}
		}
		li{
			&:not(:last-child){
				margin-bottom: 4px;
			}
		}
		a{
			font-family: 'TheNextPanBookLight';
			font-size: 16px;
			font-weight: 400;
			line-height: 130%;
			letter-spacing: 0em;
			color: #A4A4A4;
			transition: all 0.3s ease-in-out;
			@include rmax(768){
				font-size: 14px;
			}
			&:hover{
				color: $colorGreenLight;
			}
		}

		&--info{
			a{
				&:hover{
					text-decoration: none;
				}
			}
			li{
				&:not(:last-child){
					margin-bottom: 5px;
				}
			}
		}
		&--custom{
			li{
				&:not(:last-child){
					margin-bottom: 21px;
				}
			}
			a{
				font-family: 'TheNextPanBookLight';
				color: $colorWhite;
				font-size: 16px;
				line-height: 160%;
				letter-spacing: 0em;
				text-align: left;
				display: flex;
				justify-content: space-between;
				align-items: center;
				padding: 4px 12px 4px 22px;
				border: 1px solid $colorWhite;
			}
			@include rmax(768){
				display: grid;
				grid-template-columns: 1fr 1fr;
				column-gap: 23px;
				row-gap: 10px;
				li{
					&:not(:last-child){
						margin-bottom: 0px;
					}
				}
				a{
					padding: 4px 10px 4px 9px;
					color: $colorWhite;
					font-size: 14px;
				}
			}
		}
	}
	&-subscribe{
		@include rmax(768){
			margin-bottom: 40px;

		}
		&__wrap{
			padding-left: 10px;
			@include rmax(768) {
				padding-left: 0;
				.footer-menu__title {
					margin-bottom: 10px;
				}
			}
		}
		&__form-group{
			display: flex;
			flex-direction: column;
			&__input{
				background-color: transparent;
				border: none;
				border-bottom: 1px solid #FEFEFE;
				padding: 0 15px;
				margin-bottom: 33px;
				max-width: 308px;
				height: 35px;
				@include rmax(768){
					border-bottom: 1px solid #FFFFFF;
					padding: 0;
					margin-bottom: 22px;
				}
				&::placeholder{
					font-size: 16px;
					line-height: 130%;
					color: $colorGreenLight;
				}
				color: $colorGreenLight;
			}
			&__submit{
				max-width: 200px;
				@include rmax(768){
					max-width: 158px;
				}
			}
		}
	}
	&-social{
		display: flex;
		margin-bottom: 18px;
		&__wrap{
			.footer-menu__title{
				margin-bottom: 20px;
			}
		}
		li{
			&:not(:last-child){
				margin-right: 35px;
				@include rmax(768){
					margin-right: 20px;
				}
			}
		}
		a{
			svg{
				path{
					transition: all 0.3s ease-in-out;
				}
			}
			&:hover{
				svg{
					path{
						stroke: $colorGreenLight !important;
					}
				}
			}
		}
	}
	.acc-head{
		&.active{
			&+ .acc-body{
				padding-bottom: 27px;
				border-bottom: 1px solid white;
			}
		}

	}
	.acc-body{
		@include rmax(768){
			max-height: 0;
			overflow: hidden;
			transition: max-height 0.2s ease-out;
		}
	}
}
