::-webkit-input-placeholder {
	color: $colorGreyLight;
	opacity: 1;
}
::-moz-placeholder {
	color: $colorGreyLight;
	opacity: 1;
}
:-ms-input-placeholder {
	color: $colorGreyLight;
	opacity: 1;
}
:-moz-placeholder {
	color: $colorGreyLight;
	opacity: 1;
}
input,
textarea {
	position: relative;
	background: $colorWhite;
	color: $colorBlack;
	width: 100%;
	font-size: 14px;
	font-family: 'TheNextPanBook', sans-serif;
  	border: 1px solid $colorBlack;
	transition: all 0.3s ease;
	padding: 0 30px;
	appearance: none;
	//&:hover {
	//	border-color: $colorDarkGrey;
	//}
	&:active,
	&:focus{
    &:not(.btn){
      &:not([disabled]){
        &::-webkit-input-placeholder {
          color: transparent;
        }
        &::-moz-placeholder {
          color: transparent;
        }
        &:-ms-input-placeholder {
          color: transparent;
        }
        &:-moz-placeholder {
          color: transparent;
        }
      }
    }
	}
	&[disabled] {
		opacity: 0.5;
	}
}
input{
	border-radius: 2px;
	border: 1px solid #000000;
	height: 50px;
	text-overflow: ellipsis;
	overflow: hidden;
	@include rmax(768){
		height: 40px;
		font-size: 14px;
	}
}
.button{
	&.btn{
		height: 50px;
		@include rmax(768){
			height: 40px;
			font-size: 14px;
			padding: 0 10px;
		}
	}
	&.products-feature__btn{
		&.btn{
			@include rmax(768){
				height: 33px;
				font-size: 16px;
				padding: 0 10px;
			}
			@include rmax(380){
				font-size: 12px;
				min-width: auto;
			}
		}

	}
}
textarea{
	height: 110px;
	padding: 15px 15px;
	resize: none;
}
.has{
	&-error{
		input,
		textarea{
			color: $colorRed !important;
			border-color: $colorRedLight !important;
			&::-webkit-input-placeholder {
				color: $colorRedLight !important;
			}
			&::-moz-placeholder {
				color: $colorRedLight !important;
			}
			&:-ms-input-placeholder {
				color: $colorRedLight !important;
			}
			&:-moz-placeholder {
				color: $colorRedLight !important;
			}
		}
	}

	&-success {
		input,
		textarea {
			color: $colorGreen !important;
			border-color: $colorGreen !important;
		}
	}
}
.form{
	&-title{
		position: relative;
	}
	&-body{
		position: relative;
	}
	&-text{
		position: relative;
	}
	&-group{
		position: relative;
		&:not(:last-child){
			margin-bottom: 20px;
		}
	}
	&-button{
		position: relative;
		&:not(:last-child){
			margin-bottom: 20px;
		}
	}
}
.checkbox{
	position: relative;
	display: inline-block;
	[type="checkbox"],
	[type="radio"]{
		position: absolute;
		left: -9999px;
		width: inherit;
		height: inherit;
		& ~ label{
			position: relative;
			display: inline-block;
			min-height: 20px;
			color: #1a1a1a;
			line-height: 20px;
			padding-left: 20px;
			margin: -5px 0;
			cursor: pointer;
			&:before{
				content: '';
				position: absolute;
				background: $colorWhite;
				top: 50%;
				left: 0;
				width: 20px;
				height: 20px;
				border: 1px solid #c8c8c8;
				border-radius: 0;
				transform: translateY(-50%);
				transition: all 0.3s ease;
			}
			&:after{
				content: '';
				position: absolute;
				background: #ccc;
				top: 50%;
				left: 4px;
				width: 12px;
				height: 12px;
				transform: translateY(-50%);
				transition: all 0.3s ease;
				opacity: 0;
			}
			&:hover{
				&:before{
					border-color: #999;
				}
			}
			&:not(:empty){
				margin: 0;
				padding-left: 30px;
			}
		}
		&:disabled{
			& ~ label{
				&:before{

				}
				&:after{
					opacity: 0;
				}
			}
		}
		&:checked{
			& ~ label{
				&:before{

				}
				&:after{
					opacity: 1;
				}
			}
			&:disabled{
				& ~ label{
					&:before{

					}
					&:after{
						opacity: 0.2;
					}
				}
			}
		}
	}
	[type="radio"]{
		& ~ label{
			&:before,
			&:after{
				border-radius: 50%;
			}
		}
		&:checked{
			& ~ label{
				&:before{

				}
		  }
		}
	}
	&.has{
		&-error{
			[type="checkbox"],
			[type="radio"]{
				& ~ label{
					color: $colorRed !important;
					&:before{
						border-color: $colorRedLight !important;
					}
				}
			}
		}
	}
}
.help-block{
	position: absolute;
	top: 100%;
	width: 100%;
	color: $colorRed;
	font-size: 12px;
	line-height: 16px;
	padding: 0 30px;
}

.checkbox{
	&__wrap{
		display: block;
		position: relative;
		padding-left: 36px;
		margin-bottom: 12px;
		cursor: pointer;
		font-size: 16px;
		font-style: normal;
		font-weight: 400;
		line-height: 22px;
		letter-spacing: 0px;
		color: #000000;
		user-select: none;
		input{
			position: absolute;
			opacity: 0;
			cursor: pointer;
			height: 0;
			width: 0;
			&:checked{
				&~.checkmark{
					background-color: black;
					&:after{
						display: block;
					}
				}
				&~.checkbox__text{
					font-size: 18px;
					font-style: normal;
					font-weight: 600;
					line-height: 28px;
					text-align: left;
				}
			}
		}
		.checkmark{
			position: absolute;
			top: 0;
			left: 0;
			height: 24px;
			width: 24px;
			border-radius: 0px;
			background-color: #ffffff;
			border: 1px solid #000000;
			&:after{
				content: '';
				display: none;
				background-image: url('data:image/svg+xml,<svg width="13" height="10" viewBox="0 0 13 10" fill="none" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" clip-rule="evenodd" d="M12.687 0.273304C13.0883 0.6527 13.1061 1.28562 12.7267 1.68696L5.1642 9.68696C4.97531 9.88678 4.71247 10 4.4375 10C4.16253 10 3.8997 9.88678 3.7108 9.68696L0.273304 6.0506C-0.106092 5.64925 -0.0882987 5.01634 0.313046 4.63694C0.71439 4.25755 1.3473 4.27534 1.7267 4.67668L4.4375 7.54431L11.2733 0.313046C11.6527 -0.0882987 12.2856 -0.106092 12.687 0.273304Z" fill="white"/></svg>');
				background-repeat: no-repeat;
				background-size: cover;
				background-position: center;
				width: 12px;
				height: 8px;
				top: 7px;
				left: 5px;
				position: absolute;
			}

		}
	}
}
.select2 {
	&-container {
		&--default {
			.select2-selection {
				&--single {
					background: #ffffff;
					color: #282828;
					height: 51px;
					border-color: #ACC6DD;
					border-radius: 30px;
					-webkit-border-bottom-left-radius: 30px !important;
					border-bottom-left-radius: 30px !important;
					-webkit-border-bottom-right-radius: 30px !important;
					border-bottom-right-radius: 30px !important;
					@include rmax(768){
						height: 31px;
					}
					.select2-selection {
						&__rendered {
							color: #282828;
							line-height: 51px;
							padding-left: 30px;
							font-size: 18px;
							font-style: normal;
							font-weight: 400;
							letter-spacing: 0.02em;
							@include rmax(768){
								text-align: center;
								padding-left: 14px;
								line-height: 31px;
								font-size: 14px;
							}
							@include rmax(380){
								text-align: center;
								padding-left: 14px;
								line-height: 31px;
								font-size: 11px;
							}
						}
						&__arrow {
							right: 20px;
							height: 51px;
							width: 30px;
							@include rmax(768){
								height: 31px;
								right: 5px;
							}
							&:before {
								content: '\EA0A';
								color: #000000;
								position: absolute;
								top: 50%;
								left: 50%;
								font-size: 12px;
								font-family: 'svgfont', sans-serif;
								transform: translate(-50%, -50%);
								transition: all 0.3s ease-in-out;
								@include rmax(768){
									font-size: 6px;
								}
							}
							b {
								display: none;
							}
						}
					}
				}
			}
			.select2 {
				&-dropdown {
					box-shadow: 0px 4px 8px 0px #00000026;
					background: #ffffff;
					//border-color: #ACC6DD;
					border-color: transparent;
					border-top: 1px solid #282828;
					//padding: 6px 0;
					//border-radius: 0px 0px 20px 20px;
					overflow: hidden;
					z-index: 2;
				}
				&-results {
					&__option {
						position: relative;
						color: #B7B4AE;
						font-size: 18px;
						line-height: 1.5;
						padding: 8px 8px 8px 19px;
						@include rmax(768){
							font-size: 10px;
						}
						&[aria-selected=true] {
							background: transparent;
							padding-right: 40px;
							&:before {
								content: '\EA14';
								position: absolute;
								right: 20px;
								top: 12px;
								color: #B9B1CB;
								font-size: 12px;
								font-family: 'svgfont', sans-serif;
							}
						}
						&--highlighted {
							&[aria-selected] {
								background: #F2F2F2;
								color: #282828;
							}
						}
					}
				}
			}
		}
		&--open {
			//box-shadow: 0px 4px 8px 0px #00000026;
			.select2-selection {
				&--single {
					border-color: transparent;
					//border-radius: 30px;
					border-radius: 0px !important;
					//border-bottom-left-radius: 0px !important;
					//border-bottom-right-radius: 0px !important;
					.select2-selection {
						&__rendered {
						}
						&__arrow {
							right: 20px;
							height: 51px;
							width: 30px;
							@include rmax(768){
								height: 31px;
								right: 5px;
							}
							&:before {
								font-size: 12px;
								transform: translate(-50%, -50%) rotate(180deg);
								content: '\EA0A';
								color: #000000;
								@include rmax(768){
									font-size: 6px;
								}
							}
						}
					}
				}
			}
			& ~ label {
				top: -8px;
				left: 7px;
				font-size: 11px;
				line-height: 1.636;
				padding: 0 5px;
				&:before {
					top: 1px;
					opacity: 1;
					transition: opacity 0.1s ease 0.28s;
				}
				sup {
					font-size: 14px;
				}
			}

			.select2-selection--single {
				.select2-selection__rendered {
					padding-left: 19px !important;
				}
			}
		}
	}
}
