.header {
	position: relative;

	&-site {
		background-color: #000000;
	}

	&--transparent {
		background-color: transparent;
		position: absolute;
		z-index: 99;
		width: 100%;
	}

	&--fixed {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		z-index: 99;
		background-color: #000000;

		.header__logo {
			@include rmin(769) {
				max-width: 50px;
				height: 50px;
			}
		}

		//@include rmin(1500){
