		//			height: 3px;
		//			width: 100%;
		//			transition: .1s ease-in-out;
		//			background-color: #FFFFFF;
		//			&:nth-child(-n+3){
		//				display: block;
		//			}
		//		}
		//	}
		//}
	}

	&-nav {
		position: relative;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 19px 0;
		height: 75px;
		@include rmax(1280) {
			height: 60px;
		}

		&__right {
			display: flex;
			align-items: center;
			padding-right: 3px;
		}

		&__center {
			position: absolute;
			left: calc(50% + 3px);
			top: auto;
			transform: translateX(-50%);
		}
	}

	&__controls {
		display: flex;
		align-items: flex-end;
		margin-right: 10px;

		li {
			&:not(:last-child) {
				margin-right: 15px;
			}
		}
	}

	&__quiz {
		margin-left: 20px;

		&.btn {
			height: 45px;
			@include rmin(768){
				font-size: 18px;
			}
		}
	}

	&__drop-down {
		position: relative;
		width: 35px;
		row-gap: 10px;
		margin-right: 100px;
		cursor: pointer;
		display: flex;
		flex-direction: column;
		justify-content: space-between;

		@include rmax(768) {
			width: 28px;
			height: 22px;
		}

		span {
			display: none;
			position: relative;
			left: 0;
			height: 3px;
			width: 100%;
			transition: .1s ease-in-out;
			background-color: #FFFFFF;

			&:nth-child(-n+3) {
				display: block;
			}

			//&:nth-child(1) {
			//	top: 0;
			//	width: 100%;
			//}
			//&:nth-child(2) {
			//	top: 50%;
			//	width: 100%;
			//	transform: translateY(-50%);
			//}
			//&:nth-child(3) {
			//	bottom: 1px;
			//	width: 100%;
			//}
		}
	}

	&__cart {
		display: inline-block;
		position: relative;
	}

	&__in-cart {
		position: absolute;
		border-radius: 50%;
		background-color: $colorYellow;
		padding: 3px 6px;
		right: -11px;
		top: 0;
		font-size: 13px;
		font-style: normal;
		font-weight: 400;
		letter-spacing: 0em;
		text-align: center;
		line-height: 1em;
		display: flex;
		align-items: center;
		justify-content: center;
		color: $colorDarkGrey;
	}

	&__logo {
		display: flex;
		align-items: center;
		max-width: 61px;
		height: 61px;
		@include rmax(1390) {
			width: 43px;
			height: 43px;
		}
	}

}

.mobile-menu {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	max-width: 480px;
	height: 100vh;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
	background-color: #fff;
	transition-duration: .2s;
	transition-property: all;
	transition-timing-function: linear;
	opacity: 0;
	visibility: hidden;
	z-index: -1;

	&.open {
		z-index: 1000;
		opacity: 1;
		visibility: visible;
	}

	&__inner {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		overflow-y: auto;
		height: 100%;
	}

	&__close {
		position: absolute;
		top: 30px;
		right: 30px;
		cursor: pointer;
	}

	&__info {
		border-top: 1px solid #797979;
		padding: 25px 0 45px;
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		background-color: #fff;

		&--title {
			font-size: 18px;
			font-weight: 400;
			line-height: 130%;
			letter-spacing: 0em;
			text-align: center;
			color: #797979;
			margin-bottom: 20px;
		}

		&--btn-wrap {
			margin: 0 auto;
			text-align: center;

			.btn {
				&:active, &:focus {
					background-color: #000000;
					color: #ffffff;
				}
			}
		}
	}

	&__content {
		padding: 100px 83px 165px 83px;
		@include rmax(768) {
			padding: 50px 50px 165px 40px;
		}
		@include rmax(575) {
			padding: 50px 30px 165px 30px;
		}
	}

	ul {
		&:first-child {
			margin-bottom: 52px;
			@include rmax(768) {
				margin-bottom: 35px;
			}
			@include rmax(575) {
				margin-bottom: 25px;
			}

			li {
				&:not(:last-child) {
					margin-bottom: 30px;
					@include rmax(768) {
						margin-bottom: 20px;
					}
					@include rmax(575) {
						margin-bottom: 15px;
					}
				}
			}

			a {
				display: inline-block;
				width: 100%;
				color: $colorDarkGrey;
				font-size: 24px;
				font-weight: 400;
				line-height: 120%;
				letter-spacing: 0em;
				transition: all 0.3s ease-in-out;

				&:hover {
					color: #43BD9F;
					text-decoration: underline;
				}

				&:active, &:focus {
					color: #9DE7D7;
					text-decoration: underline;
				}

				@include rmax(768) {
					font-size: 20px;
				}
				@include rmax(575) {
					font-size: 18px;
				}
			}
		}

		margin-bottom: 52px;
		@include rmax(768) {
			margin-bottom: 35px;
		}
		@include rmax(575) {
			margin-bottom: 25px;
		}

		li {
			&:not(:last-child) {
				margin-bottom: 20px;
				@include rmax(768) {
					margin-bottom: 15px;
				}
				@include rmax(575) {
					margin-bottom: 10px;
				}
			}

			a {
				display: inline-block;
				width: 100%;
				font-size: 18px;
				font-weight: 400;
				line-height: 130%;
				color: #797979;
				transition: all 0.3s ease-in-out;

				&:hover {
					color: #43BD9F;
					text-decoration: underline;
				}

				&:active, &:focus {
					color: #9DE7D7;
					text-decoration: underline;
				}

				@include rmax(768) {
					margin-bottom: 16px;
				}

			}
		}
	}
}

.ajaxcart {
	&-popup {
		position: fixed;
		right: 0;
		top: 0px;
		background: #fff;
		z-index: 1001;
		width: 100%;
		max-width: 450px;
		max-height: calc(100vh - 150px);
		transition: .35s opacity, .35s visibility;
		visibility: hidden;
		opacity: 0;

		&.open {
			visibility: visible;
			opacity: 1;
		}
	}

	&__inner {
		padding: 20px 25px;
		float: left;
		width: 100%;
		height: 100%;
		overflow-x: auto;
		max-height: calc(100vh - 376px);
		&::-webkit-scrollbar { /* chrome based */
			width: 0px;  /* ширина scrollbar'a */
			background: transparent;  /* опционально */
		}
		-ms-overflow-style: none;  /* IE 10+ */
		scrollbar-width: none; /* Firefox */
	}

	&__header {
		position: relative;
		padding: 10px 30px;

		&-info {
			font-size: 15px;
			font-style: normal;
			font-weight: 400;
			line-height: 186%;
			letter-spacing: 0.02em;
			text-align: center;
			color: #3F3F3F;
		}

		&-close {
			cursor: pointer;
			right: 14px;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);

			.icon {
				font-style: 15px;
				color: #C4C4C4;
			}
		}
	}

	&__product {
		position: relative;
		max-height: 500px;
		display: grid;
		grid-template-columns: 0.15fr 0.85fr;
		column-gap: 5px;
		margin-bottom: 37px;
	}

	&-content {

		&__top {
			display: grid;
			padding-top: 5px;
			column-gap: 5px;
			grid-template-columns: 2fr 1fr;
			margin-bottom: 10px;
		}

		&__bottom {
			display: grid;
			column-gap: 5px;
			grid-template-columns: 1fr 1fr;
		}

		&__name {
			font-size: 20px;
			font-style: normal;
			font-weight: 400;
			line-height: 29px;
			letter-spacing: 0.02em;
			text-align: left;
			color: #000000;
		}

		&__price {
			&_wrap {
				text-align: right;
			}

			font-size: 20px;
			font-style: normal;
			font-weight: 400;
			line-height: 145%;
			letter-spacing: 0.02em;
			text-align: right;
			color: #000000;
		}

		&__desc{
			font-family: "TheNextPanBookLight";
			font-size: 15px;
			font-style: normal;
			font-weight: 300;
			line-height: 18px;
			letter-spacing: 0em;
			text-align: left;
			color: #282828;
		}
		&__subscription{
			&_wrap{
				align-self: end;
			}
			&_title {
				font-size: 12px;
				font-style: normal;
				font-weight: 400;
				line-height: 116%;
				letter-spacing: 0em;
				text-align: left;
				color: #797979;
				margin-bottom: 3px;
			}
			&_text{
				font-family: "TheNextPanBookLightItalic";
				font-size: 12px;
				font-style: italic;
				font-weight: 300;
				line-height: 116%;
				letter-spacing: 0em;
				text-align: left;
				color: #797979;
			}
		}
	}

	&__qty{
		&.quantity-block{
			max-width: 88px;
			height: 44px;
			margin-right: 0;
		}
		&_wrap{
			display: grid;
			grid-template-columns: 1fr 86px;
			column-gap: 11px;
		}
	}
	.ajaxremove{
		font-size: 12px;
		font-style: normal;
		font-weight: 300;
		line-height: 133%;
		letter-spacing: 0em;
		text-align: right;
		color: #7A7A7A;
		text-decoration: underline;
		text-transform: lowercase;
		transition: all 0.3s ease-in-out;
		align-self: end;
		&:hover{
			text-decoration: none;
		}

	}
	&-total{
		padding: 17px 0 24px;
		border-top: 2px solid #D2D2D2;
		border-bottom: 2px solid #D2D2D2;
		&__row{
			display: grid;
			grid-template-columns: repeat(2, 1fr);
			column-gap: 10px;
			margin-bottom: 24px;
		}
		&__col{
			font-family: "TheNextPanBookLight";
			font-size: 20px;
			font-style: normal;
			font-weight: 300;
			line-height: 120%;
			letter-spacing: 0em;
			text-align: left;
			color: #7A7A7A;
			&:last-child{
				text-align: right;
			}
		}
		&__info{
			margin-top: -10px;
			font-family: "TheNextPanBookLight";
			font-size: 13px;
			font-style: normal;
			font-weight: 300;
			line-height: 120%;
			letter-spacing: 0em;
			color: #7A7A7A;
			text-align: left;
		}
	}
	&-info {
		display: none;
		padding: 18px 33px 27px;
		border-bottom: 2px solid #D2D2D2;

		&__inner {
			max-width: 345px;
			margin: 0 auto;
			display: flex;
			column-gap: 22px;
		}

		&__plus {

			display: flex;
			justify-content: center;
			align-items: center;
			max-width: 50px;
			height: 50px;
			width: 100%;
			background-color: #545454;
			font-size: 31px;
			font-style: normal;
			font-weight: 400;
			line-height: 100%;
			letter-spacing: 0em;
			transition: all 0.3s ease-in-out;
			color: white;

			&:hover {
				color: white;
			}
		}

		&__text {
			font-family: 'TheNextPanBookLight';
			font-size: 18px;
			font-style: normal;
			font-weight: 300;
			line-height: 144%;
			letter-spacing: 0.02em;
			text-align: left;
			color: #3F3F3F;
		}
	}
	&__footer {
		background-color: #fff;
		border-top: 2px solid #D2D2D2;
		width: 100%;
		bottom: 0;
		overflow: hidden;
		left: 0;

		&_inner {
			padding: 19px 33px 40px;
		}
	}

	&-subtotal {
		display: flex;
		column-gap: 20px;
		justify-content: space-between;
		color: #000000;
		font-size: 22px;
		font-style: normal;
		font-weight: 400;
		line-height: 130%;
		letter-spacing: 0.02em;
		margin-bottom: 20px;
	}

	&__btn {
		&-wrap {
			display: flex;
			flex-direction: column;
			align-items: center;
			row-gap: 24px;
		}

		&.btn {
			box-shadow: -4px 4px 4px 0px #0000001A;
			max-width: 381px;
			width: 100%;
			margin: 0 auto;
			&:active, &:focus{
				background-color: #000000;
				color: #FFFFFF;
			}
		}
	}
	&-line{
		padding: 10px 20px 14px;
		display: grid;
		grid-template-columns: 50px 1fr;
		column-gap: 15px;
		&__text{
			font-size: 17px;
			font-style: normal;
			font-weight: 400;
			line-height: 164%;
			letter-spacing: 0.02em;
			text-align: center;
			margin-bottom: 8px;
		}
		&__item{
			height: 9px;
			width: 100%;
			overflow: hidden;
			position: relative;
			background: #E3E3E3;
			border-radius: 17.2783px;
			span{
				height: 100%;
				display: flex;
				background-color: #50A0EC;
			}
		}
	}
	&-empty{
		padding: 40px 65px 73px;
		border-bottom: 2px solid #D2D2D2;
		@include rmax(768){
			padding: 10px 0px 33px;
		}
		&__title{
			font-size: 30px;
			font-style: normal;
			font-weight: 400;
			line-height: 110%;
			letter-spacing: 0em;
			text-align: center;
			color: #282828;
			margin-bottom: 25px;
		}
		&__desc{
			font-family: 'TheNextPanBookLight';
			font-size: 18px;
			font-style: normal;
			font-weight: 400;
			line-height: 135%;
			letter-spacing: 0.02em;
			text-align: center;
			color: #797979;
			margin-bottom: 20px;
		}
		&__btn{
			&-wrap{
				text-align: center;
			}
		}
	}
	&-subscription-popup{
		max-width: 180px;
		background-color: #FFFFFF;
		&__header{
			position: relative;
			padding: 8px 25px 8px 10px;
			color: #282828;
			border-bottom: 1px #000000 solid;
			.text{
				font-family: 'TheNextPanBookLight';
				font-size: 15px;
				font-style: normal;
				font-weight: 300;
				line-height: 28px;
				letter-spacing: 0em;
				text-align: left;

			}
		}
		&__close{
			cursor: pointer;
			right: 10px;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			.icon{
				font-size: 9px;
				color: #C4C4C4;
			}
		}
		&__list{
			display: flex;
			flex-direction: column;
			li{
				font-family: "TheNextPanBookLight";
				font-size: 15px;
				font-style: normal;
				font-weight: 300;
				line-height: 28px;
				letter-spacing: 0em;
				text-align: center;
				color: #B2B2B2;
				&.active{

				}
			}
		}
	}
}
