@font-face
	font-family: "svgfont"
	src: url('fonts/svgfont.eot')
	src: url('fonts/svgfont.eot?#iefix') format('eot'), url('fonts/svgfont.woff') format('woff'), url('fonts/svgfont.woff2') format('woff2'), url('fonts/svgfont.ttf') format('truetype')
	font-weight: normal
	font-style: normal

$icon-i-close2: "\EA01"
$icon-i-arr-r: "\EA02"
$icon-i-arr-d: "\EA03"
$icon-i-ac: "\EA04"
$icon-i-left: "\EA05"
$icon-i-arr-l: "\EA06"
$icon-i-right: "\EA07"
$icon-i-star: "\EA08"
$icon-i-check: "\EA09"
$icon-i-arrow-d: "\EA0A"
$icon-i-arrow-down: "\EA0B"
$icon-i-arrow-right: "\EA0C"
$icon-i-play: "\EA0D"
$icon-i-close: "\EA0E"
$icon-i-dots: "\EA10"
$icon-i-share: "\EA11"
$icon-i-bookmark: "\EA12"


%icon
	font-family: "svgfont"
	font-style: normal
	font-weight: normal
	text-rendering: auto
	speak: none
	line-height: 1
	-webkit-font-smoothing: antialiased
	-moz-osx-font-smoothing: grayscale

.icon, [class^="icon-"], [class*=" icon-"]
	@extend %icon
	display: inline-block

.icon-i-close2:before
	content: $icon-i-close2
.icon-i-arr-r:before
	content: $icon-i-arr-r
.icon-i-arr-d:before
	content: $icon-i-arr-d
.icon-i-ac:before
	content: $icon-i-ac
.icon-i-left:before
	content: $icon-i-left
.icon-i-arr-l:before
	content: $icon-i-arr-l
.icon-i-right:before
	content: $icon-i-right
.icon-i-star:before
	content: $icon-i-star
.icon-i-check:before
	content: $icon-i-check
.icon-i-arrow-d:before
	content: $icon-i-arrow-d
.icon-i-arrow-down:before
	content: $icon-i-arrow-down
.icon-i-arrow-right:before
	content: $icon-i-arrow-right
.icon-i-play:before
	content: $icon-i-play
.icon-i-close:before
	content: $icon-i-close
.icon-i-dots:before
	content: $icon-i-dots
.icon-i-share:before
	content: $icon-i-share
.icon-i-bookmark:before
	content: $icon-i-bookmark